import React, { useState, useEffect } from "react"
import Aos from 'aos'
import 'aos/dist/aos.css'

import Movie from "../components/Movie"

export default function FlixPage(){

  let nominationsArray = JSON.parse(localStorage.getItem("nominated"))
  const [movie1Style, setMovie1Style] = useState(nominationsArray.length > 0 ? ["none", "none"] : ["default", "2px dashed rgb(70, 70, 70)"])
  const [movie2Style, setMovie2Style] = useState(nominationsArray.length > 1 ? ["none", "none"] : ["default", "2px dashed rgb(70, 70, 70)"])
  const [movie3Style, setMovie3Style] = useState(nominationsArray.length > 2 ? ["none", "none"] : ["default", "2px dashed rgb(70, 70, 70)"])
  const [movie4Style, setMovie4Style] = useState(nominationsArray.length > 3 ? ["none", "none"] : ["default", "2px dashed rgb(70, 70, 70)"])
  const [movie5Style, setMovie5Style] = useState(nominationsArray.length > 4 ? ["none", "none"] : ["default", "2px dashed rgb(70, 70, 70)"])
  const [nominatedListColor, setNominatedListColor] = useState(nominationsArray.length > 0 ? "white" : "rgb(70, 70, 70)")
  
  
  useEffect(() => {
    Aos.init({duration: 1500})
  }, [])
    return(
      <div className="flix-page">
        <h1 className="flix-page-title" data-aos="fade-up" data-aos-once="true" >Nominated Flix</h1>
        <div className="nominated-flix-container"  data-aos="fade-up" data-aos-once="true" data-aos-delay="250">
          <ol className="nominated-list" style={{color: nominatedListColor}}>
            <li className="empty-flix" style={{fontSize: "3rem", listStyle: movie1Style[0], border: movie1Style[1] }} data-aos="fade-up" data-aos-once="true" data-aos-delay="350">
              {nominationsArray.length > 0 ? 
              <Movie 
                  key={nominationsArray[0].title+nominationsArray[0].Year+String(Math.random())}
                  wallpaper={nominationsArray[0].wallpaper}
                  title={nominationsArray[0].title}
                  year={"("+nominationsArray[0].Year+")"}
                  plot={nominationsArray[0].Plot}
                  rating={nominationsArray[0].imdbRating}
                  rated={nominationsArray[0].Rated}
                  runtime={nominationsArray[0].Runtime}
                  nom={nominationsArray[0].bool}
                  flix={true}
              /> : ""}
            </li>
            <li className="empty-flix" style={{fontSize: "2.75rem", listStyle: movie2Style[0], border: movie2Style[1] }} data-aos="fade-up" data-aos-once="true" data-aos-delay="450">
            {nominationsArray.length > 1 ? 
              <Movie 
                  key={nominationsArray[1].title+nominationsArray[1].Year+String(Math.random())}
                  wallpaper={nominationsArray[1].wallpaper}
                  title={nominationsArray[1].title}
                  year={"("+nominationsArray[1].Year+")"}
                  plot={nominationsArray[1].Plot}
                  rating={nominationsArray[1].imdbRating}
                  rated={nominationsArray[1].Rated}
                  runtime={nominationsArray[1].Runtime}
                  nom={nominationsArray[1].bool}

              /> : ""}
            </li>
            <li className="empty-flix" style={{fontSize: "2.5rem", listStyle: movie3Style[0], border: movie3Style[1]}} data-aos="fade-up" data-aos-once="true" data-aos-delay="550">
            {nominationsArray.length > 2 ? 
              <Movie 
                  key={nominationsArray[2].title+nominationsArray[2].Year+String(Math.random())}
                  wallpaper={nominationsArray[2].wallpaper}
                  title={nominationsArray[2].title}
                  year={"("+nominationsArray[2].Year+")"}
                  plot={nominationsArray[2].Plot}
                  rating={nominationsArray[2].imdbRating}
                  rated={nominationsArray[2].Rated}
                  runtime={nominationsArray[2].Runtime}
                  nom={nominationsArray[2].bool}

              /> : ""}
            </li>
            <li className="empty-flix" style={{listStyle: movie4Style[0], border: movie4Style[1]}} data-aos="fade-up" data-aos-once="true" data-aos-delay="650">
            {nominationsArray.length > 3 ? 
              <Movie 
                  key={nominationsArray[3].title+nominationsArray[3].Year+String(Math.random())}
                  wallpaper={nominationsArray[3].wallpaper}
                  title={nominationsArray[3].title}
                  year={"("+nominationsArray[3].Year+")"}
                  plot={nominationsArray[3].Plot}
                  rating={nominationsArray[3].imdbRating}
                  rated={nominationsArray[3].Rated}
                  runtime={nominationsArray[3].Runtime}
                  nom={nominationsArray[3].bool}

              /> : ""}
            </li>
            <li className="empty-flix" style={{listStyle: movie4Style[0], border: movie4Style[1]}} data-aos="fade-up" data-aos-once="true" data-aos-delay="750">
            {nominationsArray.length > 4 ? 
              <Movie 
                  key={nominationsArray[4].title+nominationsArray[4].Year+String(Math.random())}
                  wallpaper={nominationsArray[4].wallpaper}
                  title={nominationsArray[4].title}
                  year={"("+nominationsArray[4].Year+")"}
                  plot={nominationsArray[4].Plot}
                  rating={nominationsArray[4].imdbRating}
                  rated={nominationsArray[4].Rated}
                  runtime={nominationsArray[4].Runtime}
                  nom={nominationsArray[4].bool}
              /> : ""}
            </li>
          </ol>
          <p>
            Nominated movies will be shown here!<br/>
            See the search tab to find movies to nominate
          </p>
        </div>
      </div>
    )
}